.sideBar {
  display: none;
}

.sideBar__active {
  background-color: #f0f1f3;
  width: 320px;
  margin: 0 auto;
}

.calculator {
  min-width: 320px;
  margin: 0 auto;
}

.title {
  margin-top: 0;
  margin-bottom: 30px;
  font-family: Verdana;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  color: #212121;
}

.container {
  max-width: 320px;
  margin: 0 auto;
  padding: 40px 15px 60px 15px;
}

.container__active {
  max-width: 320px;
  margin: 0 auto;
  padding: 40px 15px 60px 15px;
}

.formContainer {
  width: 240px;
  margin-right: 0;
}

.formInput {
  display: block;
  margin-top: 30px;
  left: 0;
  top: -28px;

  padding: 0;
  font-family: Verdana;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.21;
  letter-spacing: 0.04em;
  color: #9b9faa;

  transition: 0.3s;
}
.field {
  width: 240px;
  border: 0;
  border-bottom: 1px solid #e0e0e0;
}

.fieldError {
  color: red;
  margin-top: 10px;
  margin-bottom: 10px;
}

.radio {
  display: flex;
  justify-content: space-between;
  margin-bottom: 40px;
  margin-top: 10px;
}

.formRadio {
  display: block;
  left: 0;
  top: -28px;
  padding: 0;
  color: #9b9faa;
}

.button {
  padding: 13px 0;
  display: flex;
  justify-content: center;
  margin: 0 auto;
  margin-left: 50px;
  width: 176px;
  border: 2px solid #fc842d;
  border-radius: 30px;
  background-color: #fc842d;
  color: #ffffff;
  font-weight: 700;
  font-family: Verdana;
  font-size: 14px;
  line-height: 1.21;
  text-align: center;
  letter-spacing: 0.04em;
  outline: none;
}

.button:hover {
  background-color: #fc842d;
  color: #ffffff;
  box-shadow: 0px 4px 10px rgba(252, 132, 45, 0.5);
}

