    width: 768px;
  }

  .container {
    margin-top: 100px;
    margin-right: 0px;
    padding-left: 87px;
    padding-top: 40px;
    max-width: 768px;
    margin: 0 auto;
  
    width: 100%;
    height: 1024px;
    min-height: 944px;

    background-image: url(../../img/Group40@1x.png);
    background-repeat: no-repeat;
    background-position: top 60% right 0px;
    height: 100vh;
  }


 

  .formContainer {
    padding-bottom: 0px;
    
   }

  .container__active {
    max-width: 768px;
    margin: 0 auto;
    width: 100%;
    padding: 100px 76px 74px 87px;
  }


  .title {
    padding-right: 76px;
    font-size: 30px;
    line-height: 36px;
    margin-bottom: 60px;
  }

  .formWrapper {
    display: flex;
    margin-bottom: 60px;
  }

  .radioWrapper {
    margin-left: 31px;
    margin-top: 0;
    display: flex;
    flex-direction: column;
  }

  .button {
    margin: 0;
  }
  .radio {
    margin-top: 18px;
  }
}

@media screen and (min-width: 1280px) {
  .global {
    max-width: 1280px;
    margin: 0 auto;
  }
  .calculator {
    padding-left: 115px;
    margin: 0 auto;
    display: flex;
    padding-top: 0px;
    width: 1280px;

  }

  .container {
    max-width: 1280px;
    padding: 292px 0px 0px 0px;

    margin: 0 auto;
    position: absolute;
    z-index: -100;
    background-image: url(../../img/Group41@1x.png);
    background-repeat: no-repeat;

    background-position: top 0% right 115px;
    background-size: 900px, 500px, 700px, 700px;
    top: 0;

  }

  .container__active {
    min-width: 630px;

    padding-left: 0;
    padding-right: 0px;
  }

  .form {
    margin: 0;
  }
  .title {
    font-size: 34px;
    line-height: 41px;
    /* margin-left: 115px; */
    padding: 0px;
    margin-bottom: 68px;
    width: 605px;
  }
  /* .inputWrapper {
    margin-bottom: 24px;
  } */
  .button {
    margin-left: 346px;
  }
}

input[type="radio"] + span {
  display: inline-block;
  cursor: pointer;
  position: relative;
  padding-left: 10px;
  margin-right: 15px;
  font-size: 14px;
}
input[type="radio"] ~ span:before {
  content: "";
  display: block;
  width: 22px;
  height: 22px;
  position: absolute;
  top: -17.5px;
  left: -1px;
  border: 1px solid #aaa;
  background-color: #fff;
  border-radius: 50%;
}
input[type="radio"] {
  display: none;
}
input[type="radio"]:checked ~ span:after {
  content: "";
  display: block;
  position: absolute;
  bottom: 14%;
  left: 40%;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: #fc842d;
}

small {
  color: #aaa;
  font-family: Verdana;
  font-style: normal;
  font-weight: bold;
  font-size: 17px;
  line-height: 17px;
  letter-spacing: 0.04em;
}

input[type="radio"]:checked ~ small {
  color: #fc842d;
  font-family: Verdana;
  font-style: normal;
  font-weight: bold;
  font-size: 17px;
  line-height: 17px;
  letter-spacing: 0.04em;
}
